<template>
  <div>
    <!--   -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--   -->

    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder=""
            clearable
            v-model="queryInfo.query"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getOrderList"
            ></el-button
          ></el-input>
        </el-col>
      </el-row>

      <el-table :data="orderList" border stripe>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column
          label=""
          prop="order_price"
          width="150px"
        ></el-table-column>
        <el-table-column label="" prop="pay_status" width="100px">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status === '1'"
              ></el-tag
            >
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send" width="150px">
        </el-table-column>
        <el-table-column label="" prop="create_time" width="200px">
          <template slot-scope="scope">
            {{ scope.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="150px">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showEditDialog(scope.row)"
            ></el-button>
            <el-button
              size="mini"
              type="success"
              icon="el-icon-location"
              @click="showProgressDialog(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="pageSizes"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      @close="closeEditDialog"
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
    >
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="100px"
      >
        <el-form-item label="/" prop="address1">
          <el-cascader
            :options="cityData"
            v-model="editForm.address1"
          ></el-cascader>
          <!-- <el-input v-model="editForm.address1"></el-input> -->
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="editForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span class="dialog-footer" slot="footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button @click="editOrder"> </el-button>
      </span>
    </el-dialog>
    <!--  -->

    <!--   -->
    <el-dialog
      @close="closeProgressDialog"
      title=""
      :visible.sync="progressDialogVisible"
      width="50%"
    >
      <el-timeline>
        <el-timeline-item
          v-for="(item, index) in progressInfo"
          :key="index"
          :timestamp="item.time"
        >
          {{ item.context }}
        </el-timeline-item>
      </el-timeline>
      <span class="dialog-footer" slot="footer">
        <el-button @click="progressDialogVisible = false"> </el-button>
        <el-button @click="editOrder"> </el-button>
      </span>
    </el-dialog>
    <!--   -->
  </div>
</template>

<script>
import cityData from './citydata'
export default {
  data () {
    return {
      cityData: cityData,
      pageSizes: [8, 15, 20, 30],
      editForm: {
        address1: '',
        address2: ''
      },
      editFormRules: {
        address1: [
          { required: true, message: '', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 8
      },
      orderList: [], // 
      total: 0, // 
      editDialogVisible: false,
      progressDialogVisible: false,
      progressInfo: []
    }
  },
  created () {
    this.getOrderList() // 
  },
  methods: {
    async getOrderList () {
      const { data: result } = await this.$http.get('orders', {
        params: this.queryInfo
      })
      if (result.meta.status !== 200) {
        return this.$message.error('')
      }

      this.total = result.data.total
      this.orderList = result.data.goods
    },
    showEditDialog (item) {
      this.editDialogVisible = true
    },
    closeEditDialog (item) {
      this.$refs.editFormRef.resetFields()
    },
    async showProgressDialog (item) {
      const { data: result } = await this.$http.get('/kuaidi/1106975712662')
      if (result.meta.status !== 200) {
        return this.$message.error('')
      }
      this.progressInfo = result.data
      this.progressDialogVisible = true
    },
    closeProgressDialog (item) {
      //   this.$refs.progressFormRef.resetFields()
    },
    handleCurrentChange (newpage) {
      this.queryInfo.pagenum = newpage
      this.getOrderList()
    },
    handleSizeChange (newsize) {
      this.queryInfo.pagesize = newsize
      this.getOrderList()
    },
    editOrder () {}
  }
}
</script>
<style lang="less" scoped></style>
